.preloader { 
	background:@body-bg; 
	position: absolute;
	width: 100%;
	height: 100%;
	display: block;
	z-index: 30;
	top: 0;
	left: 0;
	.transition( all 900ms ease-in-out);
	text-align: center;
	color: @gray-light2!important;
}
.preloader i { 
	font-size: 28px;
	position: absolute;
	left: 50%;
	top: 300px;
}





/* Custom button
-------------------------------------------------- */

/* Override base .btn styles */
/* Apply text and background changes to three key states: default, hover, and active (click). */
.btn-ql,
.btn-ql:hover,
.btn-ql:active {
	background-color: transparent;
	border: 2px solid transparent;
	color: @gray-light;
	border-radius: border-radius-base;
	font-size: 0.85714285714286em;
  	font-weight: bold;
  	.transition(~"translate 100ms ease-in-out, opacity 200ms ease-in-out");
  	.transition( all 200ms);
  	position: relative;
  	overflow: hidden;

}


.btn-ql:hover {
	border-color: @gray-light2;
}

/* Apply the custom-colored gradients */
/* Note: you'll need to include all the appropriate gradients for various browsers and standards. */
.btn-ql > i {
	font-size: 1.16666666666667em;
	position: absolute;
	left: 44%;
	top: -2em;
	.animation(btn_in 300ms ease-in-out)
}
.btn-ql > span {
  .transition( all 100ms ease-in-out);
}


/*For special occacions */
.btn-ql > b.ql_sec_icon {
	font-size: 1.16666666666667em;
	font-weight: normal;
	position: absolute;
	left: 43%;
	top: 0.57142857142857em;
	.transition( all 200ms ease-in-out);
	.opacity(0);
}

.btn-ql.ql_show_sec > b.ql_sec_icon {
	.opacity(1);
}
.btn-ql.ql_show_sec > span, .btn-ql.ql_show_sec > i{
  .opacity(0);
}

/* Set the hover state */
/* An easy hover state is just to move the gradient up a small amount. Add other embellishments as you see fit. */
.btn-ql:hover > span {
  .opacity(0);
}
.btn-ql:hover > i {
	top: 0.5625em;

	.animation(btn_out 300ms ease-in-out)
}

/*
Show the icon
*/
@-webkit-keyframes btn_out {
  0%   { top: -2em; }
  100% { top: 0.71428571428571em; }
}
@-moz-keyframes btn_out {
  0%   { top: -2em; }
  100% { top: 0.71428571428571em; }
}
@-o-keyframes btn_out {
  0%   { top: -2em; }
  100% { top: 0.71428571428571em; }
}
@keyframes btn_out {
  0%   { top: -2em; }
  100% { top: 0.71428571428571em; }
}

/*
Hide the icon
*/
@-webkit-keyframes btn_in {
  0%   { top: 0.71428571428571em; }
  100% { top: 4em; }
}
@-moz-keyframes btn_in {
  0%   { top: 0.71428571428571em; }
  100% { top: 4em; }
}
@-o-keyframes btn_in {
  0%   { top: 0.71428571428571em; }
  100% { top: 4em; }
}
@keyframes btn_in {
  0%   { top: 0.71428571428571em; }
  100% { top: 4em; }
}


.btn-ql:active {
	.translate(0; 1px );
	.box-shadow(none);
}



.transition-duration(@transition-duration) {
  -webkit-animation-duration: @transition-duration;
     -moz-animation-duration: @transition-duration;
       -o-animation-duration: @transition-duration;
          animation-duration: @transition-duration;
}




